/* Skills  */

.skill-box {
	width: 100%;
	margin: 25px 0;
}

.skill-box .title {
	display: block;
	font-size: 14px;
	font-weight: 600;
	color: #333;
}

.skill-box .skill-bar {
	height: 10px;
	width: 100%;
	border-radius: 6px;
	margin-top: 6px;
	background: rgba(0, 0, 0, 0.1);
}

.skill-box .skill-per {
	position: relative;
	display: block;
	height: 100%;
	width: 95%;
	border-radius: 6px;
	animation: progress 0.4s ease-in-out forwards;
	opacity: 0;
}

.skill-per.css {
	animation-delay: 0.1s;
}



@keyframes progress {
	0% {
		width: 0;
		opacity: 1;
	}

	100% {
		opacity: 1;
	}
}

.skill-per .tooltip {
	position: absolute;
	right: -14px;
	top: -28px;
	font-size: 9px;
	font-weight: 500;
	color: #fff;
	padding: 2px 6px;
	border-radius: 3px;
	background-image: linear-gradient(to right, rgb(38, 79, 228), rgb(165, 76, 38));
	z-index: 1;
}

.tooltip::before {
	content: '';
	position: absolute;
	left: 50%;
	bottom: -2px;
	height: 10px;
	width: 10px;
	z-index: -1;
	background-color: crimson;
	transform: translateX(-50%) rotate(45deg);
}

.scroll-to-top{
	display: flex;
	align-items: center !important;
	justify-content: center !important;
}
.scroll-to-top svg{
	width: 22px !important;
}


